import React from 'react';
import './Profile.scss';
import InputField from './../../../../components/InputField/InputField';
import { Link } from 'react-router-dom';
import LinkBack from './../../../../components/LinkBack/LinkBack';
import { ACCOUNT_ROUTE } from './../../../../utils/consts';
import userStore from '../../../../store/UserStore';
import { observer } from 'mobx-react-lite';

const Profile = observer(() => {
  return (
    <>
      <LinkBack title='Personal information' path={ACCOUNT_ROUTE}/>
      <div className='profile border-gradient'>
        <h1 className='profile__title'>Personal information</h1>
        <div className='profile__container'>
          <div className='profile__img'>{userStore?.currentUser?.first_name?.charAt(0) + 
                                        userStore?.currentUser?.last_name?.charAt(0)}</div>
          <label className="profile__upload">
            <input type="file" accept="image/*"/>
            Upload photo
          </label>
        </div>
        <div className='profile__information'>
          <InputField inputType='text' placeholder='First Name' titleName='First Name' defaultValue={userStore.currentUser.first_name} />
          <InputField inputType='text' placeholder='Last Name' titleName='Last Name' defaultValue={userStore.currentUser.last_name}/>
          <InputField inputType='text' placeholder='Client ID #####' titleName='Client ID' 
            defaultValue={'Client ID #' + userStore.currentUser.user_id}/>
          <InputField inputType='date' placeholder='30.12.1994' titleName='Date of birthday' defaultValue={userStore.currentUser.birth_date}/>
          <InputField inputType='email' placeholder='ds@gmail.com' titleName='Email' defaultValue={userStore.currentUser.email}/>
          <Link to='#' className='profile__start profile__link'>To report incorrect data</Link>
          <InputField field='profile__start' inputType='text' placeholder='+380 966 666 666' titleName='Phone' />
          <Link to='#' className='profile__link profile__link-center'>Ð¡hange the number</Link>

        </div>
      </div>
    </>
  );
});

export default Profile;